
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
body{
  margin:0;
  padding:0;
}

.login-container {
  position:fixed;  
  margin: auto;
  top:0;
  right:0;
  bottom:0;
  left:0;    
  width: 350px;
  height: 251px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;  
  -webkit-box-shadow: 0 0 25px #cac6c6;  
          box-shadow: 0 0 25px #cac6c6;
  border-radius: 5px;
  -moz-border-radius: 5px;
  background-clip: padding-box;
}
.headtitle {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
} 

.system-name {
  float: left;
  font-size: 24px;
}
.user {
  float: right !important;
  text-align: right;
  padding-right: 20px;
}
.system-name,
.user .userinfo {
  line-height: 60px;
}
.userinfo {
  font-size: 16px;
  color: #fff;
}
.el-header {
  width: 100%;
  color: #fff; 
  background-color: #324157;
}
.el-aside {
  text-align: center;
  line-height: 200px;
  background: #545c64;
}
.el-main {
  padding: 10px !important;
}
/* 工具栏 */
.toolbar {
  padding: 5px 10px 15px 10px;
}
.toolbar .el-form-item {
  margin-bottom: 0px !important;
  margin-right: 5px !important;
}
.el-table {
  width: 100%;
  overflow: auto !important;
}
/* 新增和修改的对话框 */
.el-dialog .el-input,
.el-dialog .el-radio-group {
  width: 185px;
}
/* 分页 */
.block {
  margin-top: 10px;
}
/* 图片上传组件 */
.uploadImage {
  text-align: center;
}
/* 对话框底部 */
.dialog-footer {
  text-align: center;
}

.table-expand {
   font-size: 0;
}
.table-expand label {
  width: 90px;
  color: #99a9bf;
}
.table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 30%;
}
.table-expand .table-expand-longtext {
  width: 100%;
}
